.attachmentForm {
  display: flex;
  gap: 10px;
}

.round {
  position: relative;
}

.round label {
  /* background-color: #ED2939; */
  border: 1px solid #ccc;
  border-radius: 50%;
  cursor: pointer;
  height: 28px;
  left: 0;
  position: absolute;
  top: -5px;
  width: 28px;
}

.round label:after {
  border: 2px solid #fff;
  border-top: none;
  border-right: none;
  content: "";
  height: 6px;
  left: 7px;
  opacity: 0;
  position: absolute;
  top: 8px;
  transform: rotate(-45deg);
  width: 12px;
}

.round input[type="checkbox"] {
  visibility: hidden;
}

/* .round input[type="checkbox"]:checked+label {
  background-color: #ED2939;
  ;
  border-color: #535561;
  ;
} */

.round input[type="checkbox"]:checked+label:after {
  opacity: 1;
}

@media only screen and (max-width: 1154px) {
  .attachmentForm {
    display: flex;
    flex-direction: column;
  }
}

.list-view-icon {
  color: "red"
}

.hideScrollbar::-webkit-scrollbar {
  display: none;
}

.searchInput {
  display: flex;
  background-color: #232530;
  align-items: center;
  justify-content: center;
  gap: 5px;
  border: 1px solid #535561;
  border-radius: 25px;
  padding: 0px 0px 0px 10px;
  height: 40px;
  width: 358px;
  height: 45px;
}

.selectComponent {
  border: none;
  color: "#fff";
}