body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Montserrat'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #22242F;
}

@font-face {
  font-family: Montserrat;
  src: url(../src/assets/font/Montserrat-Regular.ttf);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 10px;
  height: 8px;
  background: #D9D9D9;
  border-radius: 10px;
  /* or add it to the track */
}

::-webkit-scrollbar-thumb {
  background: rgb(106, 103, 103);
  border-radius: 10px;
}

.drop-container {
  position: relative;
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffe6e6;
  padding: 20px;
  border-radius: 10px;
  border: 2px dashed #555;
  color: #444;
  cursor: pointer;
  transition: background .2s ease-in-out, border .2s ease-in-out;
}

.drop-container:hover {
  background: #eee;
  border-color: #111;
}

.drop-container:hover .drop-title {
  color: #222;
}

.drop-title {
  color: red;
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  transition: color .2s ease-in-out;
}

.file-block {
  max-width: 100%;
  color: #444;
  padding: 5px;
  background: #fff;
  border-radius: 10px;
  border: 1px solid #555;
}

.main-image {
  grid-area: image;
  /* background-image: url("https://upload.wikimedia.org/wikipedia/commons/9/92/Youth-soccer-indiana.jpg"); */
  width: 750px;
  height: 400px;
  background-position: top center;
  background-size: contain;
  background-repeat: no-repeat;
}

figure {
  /* width: 600px; */
  background-repeat: no-repeat;
}

figure {
  margin: 1em 40px;
}

figure:hover img {
  opacity: 0;
}