.draweMenu {
    display: "block";
    position: "fixed";
    top: "0px";
}

.navbars {
    /* box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px; */
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
    z-index: 100;
    display: flex;
    height: 3rem !important;
}

.active-style {
    color: #FFFFFF;
    background-color: "primary.main";
    border-radius: 50px;
}

.non-active-style {
    color: #E0E0E0;
    /* background-color: #535561; */
    border-radius: 50px;
}

.listoption:hover {
    color: #E2E2E2;
    background-color: #ED2939;
}

.listoption:hover svg {
    fill: #E2E2E2;
}

.active-snackbar {
    display: block;
}

.non-active-snackbar {
    display: none;
}

.menuIcon {
    display: none;
}

.active-draweMenu {
    display: none;
}

.drawerWidth {
    width: 18vw;
}


@media only screen and (max-width: 1240px) {
    .navbars {
        width: 74% !important;
    }
}


@media only screen and (max-width: 1206px) {
    .active-draweMenu {
        display: block;
    }

    .draweMenu {
        display: none;
    }

    .menuIcon {
        display: block;
    }

    .drawerWidth {
        width: fit-content;
        margin-top: 4.5rem;
    }

    .navbars {
        width: 95% !important;
    }

    .arrowIcon {
        display: none !important;
    }
}


